  content: "";
  position: absolute;
  top: 10px;
  margin: auto;
  left: 15px;
  display: block;
  height: 43px;
  width: 3px;
  transform: rotate(23deg);
  background-color: #FFC857;
}

@media (max-width: 575.98px) {
  body.slc .community-section-title:after {
    height: 35px;
    left: 18px;
  }
}

@media (max-width: 767.98px) {
  body.slc .community-section-title:after {
    /*display: none;*/
    height: 30px;
    left: 16px;
    top: 8px;
