body.slc section.welcome-video-section {
  position: relative;
  padding: 4rem 0 3rem;
  overflow: hidden;
}

body.slc section.welcome-video-section a {
  text-decoration: underline;
}

body.slc section.welcome-video-section .community-section-title {
  position: relative;
  display: inline-block;
}

body.slc section.welcome-video-section .community-section-title .line-top {
  position: absolute;
  right: -165px;
  top: 0;
  bottom: 0;
  display: block;
  width: 125px;
  height: 3px;
  margin: auto;
  background-color: #FFC857;
  z-index: -1;
}

body.slc section.welcome-video-section .community-section-title .line-top:before {
  content: "";
  position: absolute;
  height: 125px;
  width: 3px;
  right: 0;
  bottom: 0;
  background-color: #FFC857;
}

body.slc section.welcome-video-section .mcv-content-container h2 {
  margin-bottom: 1.5rem;
}

@media screen and (max-width: 1390px) {
  body.slc section.welcome-video-section .mcv-content-container h2 {
    margin-left: 0;
  }
}

body.slc section.welcome-video-section .mcv-content-container .mcv-intro-text {
  position: relative;
  margin-bottom: 1rem;
  padding: 0 15px;
  overflow: hidden;
  transition: max-height 0.2s ease-in;
}

body.slc section.welcome-video-section .mcv-content-container .mcv-intro-text #more-intro {
  display: none;
}

body.slc section.welcome-video-section .mcv-content-container .mcv-intro-text.expanded #more-intro {
  display: inline-block;
}

body.slc section.welcome-video-section .mcv-content-container .mcv-intro-text.fading {
  max-height: 550px;
}

body.slc section.welcome-video-section .mcv-content-container .mcv-intro-text.fading:after {
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  background: linear-gradient(transparent 75%, #fff);
}

body.slc section.welcome-video-section .mcv-content-container .content-wrapper {
  overflow-y: hidden;
  transition: all 0.2s linear;
  display: none;
}

body.slc section.welcome-video-section .mcv-video-container {
  text-align: center;
  position: relative;
  padding-bottom: 56.25%;
  margin-bottom: 1.875rem;
  height: 0;
  overflow: hidden;
}

body.slc section.welcome-video-section .mcv-video-container iframe, body.slc section.welcome-video-section .mcv-video-container object, body.slc section.welcome-video-section .mcv-video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: 767.98px) {
  body.slc section.welcome-video-section {
    padding: 2rem 0 3rem;
  }
}

body.slc section.welcome-video-section .squares {
  position: absolute;
  right: -85px;
  stroke: #ff4438;
  z-index: -1;
}

body.slc section.welcome-video-section .squares:not(.has-video).has-announce {
  bottom: -50px;
}

body.slc section.welcome-video-section .squares:not(.has-video):not(.has-announce) {
  bottom: 50px;
}

body.slc section.welcome-video-section .squares.has-video {
  bottom: unset;
  top: 350px;
}

@media (max-width: 991.98px) {
  body.slc section.welcome-video-section .squares {
    width: 130px;
    right: -65px;
  }
}

@media (max-width: 767.98px) {
  body.slc section.welcome-video-section .squares {
    display: none;
  }
}

