body.slc section.hero-section {
  width: 100%;
  position: relative;
}

body.slc section.hero-section h1 {
  font-family: "Gotham", "Arial", "Helvetica", sans-serif;
  font-size: 42px;
  font-weight: bold;
  line-height: 1.1;
  margin-bottom: 23px;
}

@media (max-width: 767.98px) {
  body.slc section.hero-section h1 {
    font-size: 32px;
  }
}

body.slc section.hero-section .featured-img-container {
  position: relative;
  height: auto;
  overflow: hidden;
  min-height: 450px;
  background-color: #084C61;
  z-index: 0;
}

body.slc section.hero-section .featured-img-container:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 60%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center 25%;
  z-index: -1;
}

@media (max-width: 991.98px) {
  body.slc section.hero-section .featured-img-container:before {
    width: 65%;
  }
}

body.slc section.hero-section .featured-img-container .hero-panel-container {
  position: relative;
  background: linear-gradient(105deg, #084C61, #084C61 50%, transparent 50%);
}

body.slc section.hero-section .featured-img-container .hero-panel-container:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: calc(50% + 45px);
  height: 100%;
  background: linear-gradient(to bottom, #084C61, rgba(8, 76, 97, 0) 100%);
  transform: skewX(165deg);
  z-index: -1;
}

body.slc section.hero-section .featured-img-container .hero-panel-container:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: calc(50% + 45px);
  height: 100%;
  background: linear-gradient(to bottom, #084C61, rgba(8, 76, 97, 0) 100%);
  transform: skewX(165deg) translateX(45px);
  z-index: -1;
}

body.slc section.hero-section .featured-img-container .hero-panel-container .hero-panel .btn-row .btn-purple {
  box-shadow: 0 10px 1px -5px #FFC857;
  border: 0;
}

body.slc section.hero-section .in-page-nav-bar {
  position: relative;
  z-index: 10;
}

body.slc section.hero-section .in-page-nav-bar.sticky {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10000;
}

@media (max-width: 767.98px) {
  body.slc section.hero-section .in-page-nav-bar.sticky {
    /*	
					  position: relative !important;
					  top: auto;
					  */
  }
